import React from "react"
import Col from "react-bootstrap/Col"
import Row from "react-bootstrap/Row"
import Container from "react-bootstrap/Container"
import {SpringLink} from "../navigation/springLink"
import Img from "gatsby-image"
import { useStaticQuery, graphql } from "gatsby"
import {FontAwesomeIcon} from '@fortawesome/react-fontawesome'
import {faLongArrowAltDown} from '@fortawesome/free-solid-svg-icons'
import Scrollchor from 'react-scrollchor'
import HomeAnimationMp4 from '../../images/home-animation.mp4'
import HomeAnimationWebm from '../../images/home-animation.webm'

import projectStyles from './projects.module.css'


const Projects = () => {
    const data = useStaticQuery(graphql`
    query {
      trimetImage: file(relativePath: { eq: "trimet-front.jpg" }) {
        childImageSharp {
          fluid(
            quality: 100
          ) {
            ...GatsbyImageSharpFluid
          }
        }
      }
      trimetLogo: file(relativePath: { eq: "trimet-rosette.png" }) {
        childImageSharp {
          fluid(
            quality: 100
            maxWidth: 50
          ) {
            ...GatsbyImageSharpFluid_noBase64
          }
        }
      }
      attensaImage: file(relativePath: { eq: "attensa-front.png" }) {
        childImageSharp {
          fluid(
            quality: 100
          ) {
            ...GatsbyImageSharpFluid
          }
        }
      }
      attensaLogo: file(relativePath: { eq: "attensa-single.png" }) {
        childImageSharp {
          fluid(
            quality: 100
          ) {
            ...GatsbyImageSharpFluid_noBase64
          }
        }
      }
      hopImage: file(relativePath: { eq: "hopreader-front.png" }) {
        childImageSharp {
          fluid(
            quality: 100
          ) {
            ...GatsbyImageSharpFluid
          }
        }
      }
      hopLogo: file(relativePath: { eq: "hop-fastpass-circle.png" }) {
        childImageSharp {
          fluid(
            quality: 100
          ) {
            ...GatsbyImageSharpFluid_noBase64
          }
        }
      }
      ctImage: file(relativePath: { eq: "ct-front.png" }) {
        childImageSharp {
          fluid(
            quality: 100
          ) {
            ...GatsbyImageSharpFluid
          }
        }
      }
      ctLogo: file(relativePath: { eq: "ct-logo.png" }) {
        childImageSharp {
          fluid(
            quality: 100
          ) {
            ...GatsbyImageSharpFluid_noBase64
          }
        }
      }
      noelImage: file(relativePath: { eq: "noel.jpg" }) {
        childImageSharp {
          fluid(
            quality: 100
          ) {
            ...GatsbyImageSharpFluid
          }
        }
      }
    }
  `);
  const trimetBg={
      backgroundImage:`url(${data.trimetImage.childImageSharp.fluid.src })`,
      backgroundSize:`cover`,
      backgroundPosition: `bottom center`
    }  ;  
  const attensaBg={
      backgroundImage:`url(${data.attensaImage.childImageSharp.fluid.src })`,
      backgroundSize:`cover`,
      backgroundPosition: `center center`
    };
    const hopBg={
      backgroundImage:`url(${data.hopImage.childImageSharp.fluid.src })`,
      backgroundSize:`cover`,
      backgroundPosition: `center center`
    };
    const ctBg={
      backgroundImage:`url(${data.ctImage.childImageSharp.fluid.src })`,
      backgroundSize:`cover`,
      backgroundPosition: `center center`
    };
    return(
        <Container fluid>
            <Row>
                <Col xs={{span:6,offset:3}}>
                    <div style={{maxWidth:'100px'}} className="mx-auto mt-5 pt-5">
                      <Img fluid={data.noelImage.childImageSharp.fluid} fadeIn={true} className='mb-3 rounded-circle'/>
                    </div>
                </Col>
                <Col xs={{span:12}} className={['d-flex','justify-content-center','align-items-center','flex-column','text-center','text-light'].join(' ')}>
                    <div className="mb-5 pb-5">
                      {/* Sm down */}
                      <div className="d-sm-none ">
                          <h4>Enriching digital experiences with thoughtful design.<br />Hi, I'm Noel.</h4>
                      </div>
                      {/* Md up */}
                      <div className="d-none d-sm-block">
                          <h2>Enriching digital experiences with thoughtful design.<br />Hi, I'm Noel.</h2>
                      </div>
                      <p>
                          <Scrollchor to="#work">
                              <FontAwesomeIcon icon={faLongArrowAltDown} size="lg"/>
                          </Scrollchor>
                      </p>
                    </div>
                </Col>
            </Row>
            <Row id='work'>
                <Col xs={{span:12}} sm={{span:6}} className={['text-white','pb-3',projectStyles.projectHeight].join(' ')} >
                  <SpringLink to="/trimet" >
                    <div className={['h-100','d-flex','flex-column', projectStyles.projectTitleLogo].join(' ')} style={trimetBg}>
                      <div className={['p-2','d-flex','flex-row','mb-auto', projectStyles.projectTitleLogo].join(' ')}>
                        <div className="align-self-center">
                          <span className="h5">TriMet</span>
                        </div>
                        <div className="ml-auto align-self-center">
                          <Img fluid={data.trimetLogo.childImageSharp.fluid} style={{width:'35px'}}/>
                        </div>
                      </div>
                      <div className="d-flex">
                        <p className={['px-2','small', projectStyles.perProjectText].join(' ')}>UX Design, Visual Design, Motion Graphics, Video/Content Production, Animation Storytelling, React Front-end Development, Photography</p>
                      </div>
                    </div>
                  </SpringLink>
                </Col>
                <Col xs={{span:12}} sm={{span:6}} className={['text-white','pb-3',projectStyles.projectHeight].join(' ')}>
                  <SpringLink to="/attensa" >
                    <div className="d-flex flex-column h-100" style={attensaBg}>
                      <div className={['p-2','d-flex','flex-row','mb-auto', projectStyles.projectTitleLogo].join(' ')}>
                        <div className="align-self-center">
                          <span className="h5">Attensa</span>
                        </div>
                        <div className="ml-auto align-self-center">
                          <Img fluid={data.attensaLogo.childImageSharp.fluid} style={{width:'35px'}}/>
                        </div>
                      </div>
                      <div className="d-flex">
                        <p className={['px-2','small', projectStyles.perProjectText].join(' ')}>UX Design, Visual Design, Content/Strategic Marketing</p>
                      </div>
                    </div>
                  </SpringLink>
                </Col>
            </Row>
            <Row>
                <Col xs={{span:12}} sm={{span:6}} className={['text-white','pb-3',projectStyles.projectHeight].join(' ')}>
                  <SpringLink to="/cascadiathreads" >
                    <div className="d-flex flex-column h-100" style={ctBg}>
                      <div className={['p-2','d-flex','flex-row','mb-auto', projectStyles.projectTitleLogo].join(' ')}>
                        <div className="align-self-center">
                          <span className="h5">Cascadia Threads</span>
                        </div>
                        <div className="ml-auto align-self-center">
                          <Img fluid={data.ctLogo.childImageSharp.fluid} style={{width:'45px'}}/>
                        </div>
                      </div>
                      <div className="d-flex">
                        <p className={['px-2','small', projectStyles.perProjectText].join(' ')}>Design Lead</p>
                      </div>
                    </div>
                  </SpringLink>
                </Col>
                <Col xs={{span:12}} sm={{span:6}} className={['text-white','pb-3',projectStyles.projectHeight].join(' ')}>
                  <SpringLink to="/hop" >
                    <div className="d-flex flex-column h-100"  style={hopBg}>
                      <div className={['p-2','d-flex','flex-row','mb-auto', projectStyles.projectTitleLogo].join(' ')}>
                        <div className="align-self-center">
                          <span className="h5">Hop Fastpass</span>
                        </div>
                        <div className="ml-auto align-self-center">
                          <Img fluid={data.hopLogo.childImageSharp.fluid} style={{width:'35px'}}/>
                        </div>
                      </div>
                      <div className="d-flex">
                        <p className={['px-2','small', projectStyles.perProjectText].join(' ')}>Hand modeling, video production, motion graphics, 2D animation, UI Design</p>
                      </div>
                    </div>
                  </SpringLink>
                </Col>
            </Row>
            <Row>
              <Col xs={{span:12}} sm={{span:8,offset:2}} md={{span:6,offset:3}} lg={{span:4, offset:4}} className={['py-5','my-5','d-block','align-items-center'].join(' ')}>
                  <div className={['img-fluid','w-100','d-block'].join(' ')}>
                      <video loop muted autoPlay className="w-100 h-100">
                          <source src={HomeAnimationWebm} type="video/webm" />
                          <source src={HomeAnimationMp4} type="video/mp4" />
                          <span className="text-light">Your browser does not support HTML5 video.</span>
                      </video>
                      <p className="text-light text-center py-5">Currently at <SpringLink to="/trimet">TriMet</SpringLink>.</p>
                  </div>
              </Col>
            </Row>
        </Container>
        )
}

export default Projects



